<template>
    <view class="">
		<view class="switch">
			<text>开关</text>
			<u-switch v-model="checked"  active-color="red"></u-switch>
		</view>
        <u-field
			v-model="setpwd"
			label="设置密码:"
			placeholder="请填写密码"
			:password='true'
			:error-message="OlderrorMessage"
        >
        </u-field>
        <view class="" style="padding: 0 30rpx;">
            <u-button :custom-style="customStyle" @click="sure">确定</u-button>
        </view>
    </view>
</template>

<script>
	import {setSecondPwd} from '@/utill/api/secondPwd/secondPwd.js'
	import {getInfor} from '@/utill/api/personalInfor/getInfor.js'
    export default {
        data() {
            return {
                setpwd: '',
                OlderrorMessage: '',
                customStyle: {
                    marginTop: '20px', // 注意驼峰命名，并且值必须用引号包括，因为这是对象
                    color: '#ffffff',
                    background: '#86B0D4'
                },
				checked:false,
            }
        },
        methods: {
            sure() {
                if (this.setpwd == '') {
                    this.OlderrorMessage = '密码不能为空'
                    return;
                }
				console.log(this.checked)
				console.log(uni.getStorageSync('doctorId'))
				setSecondPwd({
					doctorId:uni.getStorageSync('doctorId'),
					secondLevelPasswordSwitch:this.checked,//开关
					secondLevelPassword:this.setpwd,//设置密码
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						uni.switchTab({
							url:'/pages/mine/mine'
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
            },
			getDocinfor(){
				getInfor({}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						this.checked=res.data.object.secondLevelPasswordSwitch
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{ 
					console.log(err)
				})
			}
        },
		onLoad() {
			// 获取密码设置的状态
			this.getDocinfor()
		}
    }
</script>

<style scoped lang="scss">
	.switch{
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20rpx 30rpx;
		border-bottom: 1px solid #e0dbdb;
	}
</style>
